:root {
  --gray: rgb(252, 252, 252);
  --title-size: 36px;
  --left-padding: 15px;
  --blue: rgb(0, 158, 221);
  --nav-width: 500px;
  --paddding-top: 15px;
}

html {
  height: 100%
}

.App {
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif
}

body {
  background-color: var(--gray);
  font-size: 18px;
}

a {
  color: var(--blue);
}

.headerContainer {
  background-color: var(--gray);
  width: var(--nav-width);
  font-size: var(--title-size);
  font-weight: bold;
  position: fixed;
  padding-left: var(--left-padding);
  padding-right: var(--left-padding);
  padding-top: var(--paddding-top);
  padding-bottom: 5px;
  z-index: 1;
}

.sideNav {
  background-color: var(--gray);
  position: fixed;
  width: var(--nav-width);
  z-index: 2;
  font-size: 22px;
  left: 0;
  top: calc(2 * var(--title-size) + var(--paddding-top) + 5px);
  padding-top: var(--paddding-top);
  padding-left: var(--left-padding);
}


.navUnselected {
  text-decoration: none;
  color: black;
}

.navSelected {
  color: var(--blue);
  text-decoration: underline;
}

.content {
  position:sticky;
  top: 0;
  bottom:0;
  padding-left: calc(var(--nav-width) + var(--left-padding) + 20px);
  padding-right: var(--left-padding);
  z-index: 0;
  width: 800px;
  padding-top: var(--paddding-top);  
}

.pageTitle {
  margin-top: 0;
}


